.WatchListCard {
    color: #fff;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: start;
        align-items: flex-start;
    text-align: left;
    position: relative;
}

.WatchListCard__img {
    display: block;
    width: 40%;
    height: 100px;
    -o-object-fit: cover;
       object-fit: cover;
    margin-right: 8px;
}
.WatchListCard__info {
    display: block;
}
.WatchListCard__title {
    font-size: 16px;
    line-height: 1.5;
}
.WatchListCard__reliase,
.WatchListCard__rating {
    font-size: 14px;
    margin-top: 8px;
}
.WatchListCard__btn {
    background-color: #c0ca33;
    border: 1px solid #8c9900;
    border-radius: 4px;
    -webkit-box-shadow: 0 2px 2px 0 rgba(33,33,33,.3);
            box-shadow: 0 2px 2px 0 rgba(33,33,33,.3);
    cursor: pointer;
    font-weight: bold;
    font-size: 20px;
    height: 35px;
    position: absolute;
    right: 0px;
    bottom: 0px;
    width: 35px;
    z-index: 100;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
}
.WatchListCard__btn:hover {
    background-color: #00e5ff;
    border: 1px solid #00b2cc;
}



